import React, { useState, useEffect,useRef } from 'react'
import TodoEdit from "../components/TodoEdit"
import TodoList from "../components/TodoList"
import TodoService from "../service/user-service"


const Home = () => {
    const initTodoState = {
        id: "",
        title: "",
        description: "",
        completed:"false",

    }
    const [input, setInput] = useState(initTodoState);
    const [todos, setTodos] = useState([])
    const [editTodo, setEditTodo] = useState(null);



    useEffect(() => {
        reciveTodos()
    }, [])

    const reciveTodos= () =>{
        TodoService.getTodo()
        .then(res=>{
            setTodos(res.data.data)
        }).catch(err=>{
            console.log(err)
        })

    }


    return (
        <div className="Todo-container">
            <div className="app-wrapper">
                <div>
                    <TodoEdit
                        input={input}
                        setInput={setInput}
                        todos={todos}
                        setTodos={setTodos}
                        editTodo={editTodo}
                        setEditTodo={setEditTodo}
                        initTodoState={initTodoState}
                        reciveTodos={reciveTodos}
                    />
                </div>
                <div>
                    <TodoList
                        todos={todos}
                        setTodos={setTodos}
                        setEditTodo={setEditTodo}
                        reciveTodos={reciveTodos}
                    />
                </div>
            </div>
        </div>
    )
}

export default Home
