import React from 'react';
import "../components/Footer.css"

const Footer =()=>{
    return (
        <div className="footer-dark">
            <div className="container">
                <div className="row  justify-content-center">
                    {/*  Column 1 */}
                    <div className="col-md-4 col-sm-6 item">
                        <h4>MENU 選項</h4>
                        <ul>
                            <li>Home 主頁</li>
                            <li>Instructions 使用教學</li>
                            <li>Privacy 隱私權保護政策</li>
                            <li>Contact 聯繫我們</li>
                        </ul>
                    </div>
                    {/*  Column 2 */}
                    <div className="col-md-4 col-sm-6 item">
                        <h4>ABOUT</h4>
                        <ul>
                            <li>GainTime</li>
                            <li>Developer 開發者</li>
                        </ul>
                    </div>
                    {/*  Column 3 */}
                    <div className="col-md-4 col-sm-6 item text">
                        <h4>GainTime</h4>
                        <p>GainTime是一個簡易的待辦事項網頁，您可以無須登入直接使用但清單項目不會儲存，如需儲存請註冊用戶並登入。</p>
                        {/* <p>GainTime 是一款幫助你「保持專注」的工具，其使用方法概念源自於Pomodoro Technique，該方法使用一個定時器來分割出一個一般為25分鐘的工作時間和5分鐘的休息時間。 </p> */}
                    </div>
                    <div>
                    <div className="col item social">
                        <a href="#"><i className="fab fa-facebook"></i></a>
                        <a href="#"><i className="fab fa-twitter"></i></a>
                        <a href="#"><i className="fab fa-github"></i></a>
                        {/* <a href="#"><i className="fab fa-instagram"></i></a> */}
                    </div>
                        <p className="copyright">GainTime © {new Date().getFullYear()}  All rights reserved.</p>

                    </div>

                </div>
            </div>
        </div>
    )
}

export default Footer;