import React, { useEffect } from 'react'
import { v4 as uuidv4 } from "uuid";
import TodoService from '../service/user-service'

const TodoEdit = ({ input, setInput, todos, setTodos, editTodo, setEditTodo, initTodoState, reciveTodos }) => {

    const updateTodo = (id, title, description, completed) => {
        const user = JSON.parse(localStorage.getItem('user'));
        if (user !== null) {
            const data = {
                id: id,
                title: title,
                description: description
            }
            TodoService.updateTodo(id, data)
                .then(res => {
                    setTodos([{
                        id: res.data.data.id,
                        title: res.data.data.title,
                        description: res.data.data.description
                    },])
                    const newTodo = todos.map((todo) =>
                        todo.id === id ? { id, title, description, completed } : todo
                    )
                    setTodos(newTodo);
                    setEditTodo("")
                    // reciveTodos()

                    console.log(res.data.data)
                }).catch(e => {
                    console.log(e)
                })
        } else {
            const newTodo = todos.map((todo) =>
                todo.id === id ? { id, title, description, completed } : todo
            )
            setTodos(newTodo);
            setEditTodo("")
        }





    }

    useEffect(() => {
        if (editTodo) {
            setInput(editTodo)
        } else {
            setInput(initTodoState)
        }
    }, [setInput, editTodo])

    const onInputChange = (e) => {
        const { name, value } = e.target;
        setInput({ ...input, [name]: value });
    }

    const onTodoEditSubmit = (e) => {
        e.preventDefault();
        if (!editTodo) {
            const user = JSON.parse(localStorage.getItem('user'));
            if (user !== null) {
                var data = {
                    title: input.title,
                    description: input.description,
                }
                TodoService.createTodo(data)
                    .then(res => {
                        setTodos([{
                            id: res.data.data.id,
                            title: res.data.data.title,
                            description: res.data.data.description
                        }, ...todos])
                        setInput(initTodoState)
                    }).catch(e => {
                        console.log(e)
                    })
            } else {
                setTodos([{ id: uuidv4(), title: input.title, description: input.description, completed: false }, ...todos]);
                setInput(initTodoState);
            }
        } else {
            updateTodo(editTodo.id, input.title, input.description, editTodo.completed)
        }
    }

    return (
        <div className="header">

            <h1>Todo - List</h1>
            <form onSubmit={onTodoEditSubmit}>
                <input
                    type="text"
                    placeholder="Enter a Todo..."
                    className="task-input"
                    value={input.title}
                    required
                    onChange={onInputChange}
                    name="title"
                />
                <input
                    type="text"
                    placeholder="Enter a Description..."
                    className="task-input"
                    required
                    value={input.description}
                    onChange={onInputChange}
                    name="description"
                />
                <button className="button-add" type="submit" >
                    {editTodo ? "OK" : "Add"}
                </button>
            </form>
        </div>

    )
}

export default TodoEdit
