import axios from "axios";
import authHeader from "./auth-header";

const API_URL = "https://golang-gaintime.herokuapp.com/api/";

const getTodo = () => {
  return axios.get(API_URL + "todos/",
  {headers:authHeader(),
  });
};

const createTodo = (data) => {
  return axios.post(API_URL + "todos/",
  data,
  {headers:authHeader()
  });
};
// {
//   "title" : "Golang Learning",
//   "Description" : "web Golang-GIN framework"
// }


const updateTodo = (id,data) =>{
  return axios.put(API_URL + `todos/${id}`,
  data,
  {headers:authHeader(),
  });
};
// {
//   "id":15,
//   "title" : "Golang123 Learning",
//   "Description" : "web Golang-GIN framework"
// }

const deleteTodo = (id)=>{
  return axios.delete(API_URL + `todos/${id}`,
  {headers:authHeader(),
  });
};


export default {
  getTodo,
  createTodo,
  updateTodo,
  deleteTodo
}
